import MyHeader from '../../../components/header/header.vue'
import MyLike from '../../../components/cainiXihuan/xihuan.vue';
import {
	xihuanShuju,
	success
} from './config.js';
import http from '../../../utils/http.js'
export default {
	name: 'xiangqing',
	components: {
		MyLike,
		MyHeader
	},
	data() {
		return {
			// 设置猜你喜欢数据
			items: xihuanShuju,
			// 订单数据
			orderData: success,
			// 右上角红标数字
			// newsNumber: 66,
			orderId: '',
			oderDetail: ''
		}
	},
	onLoad: function(e) {
		this.orderId = e.order_id
		console.log("从前一个页面传过来的order_id：", e.order_id,e.statu)
	},
	methods: {
		// 获取我的订单详情
		getOrderDetail: function() {
			http.httpTokenRequest({
				url: 'orderDetail?order_id=' + this.orderId,
				method: 'get'
			}, {}).then(res => {
				if (res.data.code == 200) {
					this.oderDetail = res.data.data
					console.log("获取订单详情成功", res.data.data)
				}
			}, error => {
				console.log(error);
			})
		}
	},
	created() {
		this.getOrderDetail()
	}
};
